body {
  margin: 0;
  padding: 0;
  display: -ms-flexbox;
  display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    text-align: center;
    background: #F5F5F5;
    font-family: 'Roboto', sans-serif;
}

.list_menu {
    width: 2400px;
    height: 80px;
    background: white;
    margin-top:-0.6%;
}

.list_menu .nav_input {
    border-radius: 5px;
    text-align: center;
    background-color: #F5F5F5;
    height: 25px;
    margin: 10% 14%;
    width: 280px;
    border:1px solid #DCDCDC;
    outline:none;
}

.list_menu li {
    display: -ms-inline-flexbox;
    display: inline-flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.nav_button {
    text-decoration: none;
    border: 2px solid #DCDCDC;
    padding: 5px;
    border-radius: 5px;
}

.li2 {
    margin: 0 12%;
}


.ProfileName {
    margin-top: 50px;
    border-bottom: 1px solid lightgray;
    height: 200px;
    width: 935px;
    margin-left: 31%;
    text-align: left;
}

.profile_avatar {
    width: 150px;
    height: 150px;
    border: 2px solid #DCDCDC;
    border-radius: 100%; 
    margin-left: 5%;
}

.ProfileName ul {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    position: relative;
    bottom: 80%;
}

.ProfileName li {
    margin-left: 2%;
}

.content {
    margin-top: 1%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    width: 935px;
    margin-left: 31%;
}

.content img {
    margin-bottom: 28px;
    border: 1px solid #DCDCDC;
    overflow: auto;
    border-radius: 5px;
    background: white;
}

.close_modal {
    position: absolute;
    top:0px;
    right:0px;
}

.add__text {
    border: 0;
    border-top: 1px #F5F5F5 solid;
    padding: 15px;
    position: absolute;
    bottom: 0;
    right: 0;
    height: 30px;
    width: 250px;
    font-size: 15px;
    outline:none;
}

.add__btn {
    position: absolute;
    bottom: 60px;
    right: 0;
}

footer {
    text-transform: uppercase;
    width: 935px;
    margin-left: 30%;
    height: 50px;
    margin-top: 2%;
}

footer ul {
    list-style: none;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    margin: 0;
    padding: 0;
    padding-top: 15px;
}

footer li {
    font-size: 13px;
    float:left;
    cursor: pointer;
    color: darkslateblue;
}


.login_field input {
    width: 250px;
    margin-bottom: 5%;
    padding: 15px;
    border-radius: 5px;
    outline: none;
    border:3px solid #DCDCDC;
}

.login_field a {
    border: 2px solid gray;
    border-radius: 5px;
    outline: inherit;
    cursor: pointer;
    padding: 5px;
}

.post_blog {
    background: #FFFFFF;
    padding: 20px;
    margin: 10px 0 10px 40%;
    -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.10);
            box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.10);
    width: 500px;
    position: relative;
}

.blog-form {
    background: #90caf9 ;
    padding: 2%;
    width: 445px;
    margin-left: 40%;
    border-radius: 5px;
    border:1px solid #DCDCDC;
    margin-top: 0.5%;
}

.blog-form textarea {
    border-radius: 5px;
    height: 40px;
    border:1px solid #DCDCDC;
    max-height: 300px;
    max-width: 400px;
}

.blog-form button {
    border-radius: 5px;
    border:3px solid #DCDCDC;
    background: #F5F5F5;
    margin-left: 5px;
}

.content_blog {
    margin-left: 5px;
    word-wrap: break-word;
}

.label_blog {
    position:absolute;
    top:0px;
    left: 0px;
    padding: 2px;
    margin: 3px;
    color: white;
    background: #1E90FF;
    font-size: 12px;
}

.blog_search {
    padding: 8px;
    border-radius: 5px;
    outline: none;
    border:1px solid #DCDCDC;
    margin-left: 2.2%;
    text-align: center;
}



